.body {
  font-family: "Nunito Sans";
  text-align: center;
  margin-top: 80px;
  .title {
    font-weight: 800;
    font-size: calc(18px + 6 * (100vw / 1200));
    margin-bottom: 3vw;
    line-height: 130%;
  }
  .message {
    font-weight: 500;
    font-size: calc(16px + 2 * (100vw / 1200));
    line-height: 130%;
  }
}
