.page {
  .container {
    margin-top: 130px;
    .title {
      text-align: center;
    }
    .header {
      margin-top: 15px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .linkToHome {
        img {
          width: 10px;
          height: 10px;
        }
      }
      .linkToHome {
        display: flex;
      }
      .headerItem {
        display: flex;
        align-items: center;
        gap: 2vw;
        font-family: "Nunito Sans";
        font-style: normal;
        font-weight: 600;
        font-size: calc(14px + 2 * (100vw / 1200));
        text-decoration: underline;
        line-height: normal;
        color: #000000;
        opacity: 0.8;
        background: none;
        &:hover {
          opacity: 1;
        }
      }
      .clearButton {
        align-self: end;
        justify-self: flex-end;
      }
    }
    .items {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      row-gap: 30px;
      margin-top: 35px;
      margin-bottom: 50px;
    }
    .total {
      display: flex;
      gap: 10vw;
      font-family: "Nunito Sans";
      font-weight: 700;
      font-size: calc(20px + 2 * (100vw / 1200));
      line-height: 25px;
      color: #000000;
      margin-bottom: 80px;
    }
  }
}

@media (max-width: 767px) {
  .page {
    .container {
      margin-top: 90px;
      .header {
        margin-top: 5px;
        .linkToHome {
          display: none;
        }
      }
      .items {
        row-gap: 20px;
        margin-top: 25px;
        margin-bottom: 40px;
      }
      .total {
        margin-bottom: 80px;
      }
    }
  }
}

@media (max-width: 480px) {
  .page {
    .container {
      margin-top: 80px;
      .header {
        margin-top: 5px;
        .linkToHome {
          display: none;
        }
      }
      .items {
        row-gap: 20px;
        margin-top: 25px;
        margin-bottom: 40px;
      }
      .total {
        margin-bottom: 80px;
      }
    }
  }
}
